import './App.css';
import { HomeLayout } from './layouts/HomeLayout';
import CCDNavbar from './components/CCDNavbar';
import { createContext, useEffect, useMemo, useState } from 'react';

export const HomeContext = createContext({
  isDesktop: false,
  setIsDesktop: () => {}
});

function App() {
  const [isDesktop, setIsDesktop] = useState(false);
  const [desktopParsed, setDesktopParksed] = useState(false);

  const homeMemoValues = useMemo(
    () => ({
      isDesktop,
      setIsDesktop
    }),
    [isDesktop]
  );

  useEffect(() => {
    setIsDesktop(window.innerWidth > 992);
    setDesktopParksed(true);
  }, [isDesktop]);

  return (
    <div className="App">
      <HomeContext.Provider value={homeMemoValues}>
        {desktopParsed ? (
          <>
            <header id="navbar" className="navbar navbar-inverse container-fluid">
              <CCDNavbar />
            </header>
            <HomeLayout />
          </>
        ) : (
          ''
        )}
      </HomeContext.Provider>
    </div>
  );
}

export default App;
